import {
  DocumentsCollectionAPIResponse,
  DocumentsCollectionInfo,
} from "@rp/react/tanstack-api/documents"

const parseEventName = (
  collectionDocuments: DocumentsCollectionAPIResponse | undefined,
): string => {
  if (!collectionDocuments) return "N/A"

  const firstWithEvent: DocumentsCollectionInfo | undefined = collectionDocuments.documents.find(
    doc => doc.reportingPeriod?.[0] && doc.reportingPeriod[1],
  )

  if (!firstWithEvent?.reportingPeriod) return "N/A"

  const year = firstWithEvent.reportingPeriod[0].match(/20\d\d/)?.[0]
  const quarter = firstWithEvent.reportingPeriod[1].match(/Q\d/)?.[0]

  if (!year || !quarter) return "N/A"

  return `${quarter} ${year}`
}

export { parseEventName }
